.album{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
     -ms-flex-pack: center;
         justify-content: center;
     -ms-flex-align: center;
         align-items: center;
    font-size: 1.9vh;
    border-radius: 2rem;
}

.album:hover a{
    text-decoration: underline;
}

.album:hover .price{
    border:1px solid #FF004F;
}



.album-item{
    margin-top: .3rem;

}


.imgs{
    background-image: url('https://media.giphy.com/media/y1ZBcOGOOtlpC/200.gif');
    -webkit-transition:all .3s;
    -o-transition:all .3s;
    transition:all .3s;
}



.imgs:hover{
    opacity: .9;
}


.album-img{
    width: 200px;
    height: 200px;
    border-radius: 2rem;
    cursor: pointer;
}




.link{
    text-decoration: none;
     color: black;
     -webkit-transition: all .3s;
     -o-transition: all .3s;
     transition: all .3s;
}


.price{
    border:  1px solid white ;
    padding: .2rem;
    -webkit-box-shadow: 0 .01rem .2rem rgb(163, 162, 162);
            box-shadow: 0 .01rem .2rem rgb(163, 162, 162);
    border-radius:.2rem;
}




